.resume {
    width: 60%;
    height: auto;
    margin: 100px auto;
}

/*Media Queries*/

@media screen and (max-width: 350px) {

    .resume {
        width: 90%;
        margin: 50px auto;
    }




}

@media screen and (min-width: 351px) and (max-width: 576px) {

    
    .resume {
        width: 90%;
        margin: 50px auto;
    }
 

}

@media screen and (min-width: 577px) and (max-width: 768px) {
    
    .resume {
        width: 90%;
        margin: 50px auto;
    }



}


@media screen and (min-width: 769px) and (max-width: 1010px) {

    
    .resume {
        width: 90%;
        margin: 50px auto;
    }

}

@media screen and (min-width: 1011px) and (max-width: 1179px) {
    
    .resume {
        width: 90%;
        margin: 50px auto;
    }


}