/*
Phones portrait 
Breakpoint 350px

Phones Landscape
Breakpoint 576px

Medium Screens (Tablets)
Breakpoint 768px

Medium - Large Screens 
Breakpoint 1010px

Large Screens (Laptops)
Breakpoint 1433px

Extra Large Screens (Monitors)


*/

@media screen and (max-width: 350px) {
        /*Homepage*/

        .logo-image {

            display: none;

        }

        .hero-container {
            height: 60vh;
        }

        .section-container-left {
            height: 60vh;
            grid-template-columns: 1fr;
            grid-gap: 0;
    
        }
    
        .section-container-right {
            height: 60vh;
            grid-template-columns: 1fr;
            grid-gap: 0;
            padding-top: 30px;
    
        }

        .section-container-center {
            height: 60vh;
        }
    
        /*Recent work page*/
    
        .project-section-container-left {
            height: auto;
            padding-top: 60px;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 0.5fr 0.5fr;
            row-gap: 30px;
            grid-template-areas: "intro"
                                 "project-image"
                                 "skills";
    
            
    
        }
    
        .project-section-container-right {
            height: auto;
            padding-top: 60px;
            grid-template-columns: 1fr;
            grid-template-rows: 1fr 0.5fr 0.5fr;
            row-gap: 30px;
            grid-template-areas: "intro"
                                 "project-image"
                                 "skills";
        }
    
       
    
        .image-project-wrapper {
            grid-row: 2;
        }
            
        .project-image {
            grid-area: project-image;
            grid-row: 2;
            width: 100%;
            height: auto;
            margin: 0 auto;
            border: 1px solid black;
        }
    
    
        .main-text-container-left {
    
            grid-area: intro;
        }
    
        .main-text-container-right {
    
            grid-area: intro;
    
        }
    
        .skills-container-small-right {
            width: 100%;
            margin: 0 auto;
            align-self: start;
            grid-area: skills;
        
            
    
        }
    
        .skills-container-small-left {
            width: 100%;
            margin: 0 auto;
            align-self: start;
            grid-area: skills;
        
    
            
    
        }
    
        /*Both Homepage and Recentwork */
    
        .main-header {
            text-align: center;
            font-size: 2rem;
            letter-spacing: 0.3rem;
        }
    
        .main-paragraph {
            text-align: center;
            font-size: 1rem;
        }
        
        .primary-btn {
            margin: 0 auto;
            width: 40%;
        }

        .hero-heading {
            font-size: 2.7rem;
        
        }

        .sub-heading {
            font-size: 1.2rem;
        }
    
    
    
       
    }




