.contact-container {
    border: 4px solid #FFDA79;
    padding: 40px 80px;
    background-color: #0d0c22;
    color: #f8f7f4;
    width: 55vw;

    


}

.contact-details {
    text-align: left;
    font-size: 1.5rem;
    font-weight: 600;



}

.contact-center {
    border-top: 2px solid #FFDA79;
    background: #f8f7f4;
    display: grid;
    place-items: center;
    height: 80vh;
    
}

/*Media Queries*/

@media screen and (max-width: 350px) {

    .contact-center {

        height: 60vh;

    }

    .contact-details {
        text-align: center;
        font-size: 1rem;
        text-align: center;
    }

    .contact-container {
        padding: 10px;
        width: 70vw;
    }

}

@media screen and (min-width: 351px) and (max-width: 576px) {

    .contact-center {

        height: 60vh;

    }

    .contact-details {
        text-align: center;
        font-size: 1rem;
        text-align: center;
    }

    .contact-container {
        padding: 20px;
        width: 80vw;
    }

}

@media screen and (min-width: 577px) and (max-width: 768px) {
    .contact-center {

        height: 60vh;

    }

    .contact-details {
        text-align: center;
        font-size: 1rem;
        text-align: center;
    }

    .contact-container {
        padding: 20px;
        width: 80vw;
    }


}


@media screen and (min-width: 769px) and (max-width: 1010px) {

    .contact-details {
        text-align: center;
        font-size: 1.2rem;
    }

    .contact-container {
        padding: 40px;
        width: 70vw;
    }
}

