.footer-container {
    background-color: #0d0c22;
    padding: 50px 0;
}

.footer-links-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 30vw;
    margin: 0 auto;

}

.footer-link {
    color: white;
    text-decoration: none;
    margin-top: 40px;
}

.footer-link:hover {
    font-weight: 600;
    color: #b3fbe3;
    cursor: pointer;
}

.footer-logo-image {
    width: 12%;
    height: auto;


}


/*Media Queries*/

@media screen and (max-width: 350px) {

    .footer-logo-image {
        width: 50%;
    
    
    }
    

    .footer-links-container {
        width: 90vw;
        font-size: 0.7rem;
    

    }


}

@media screen and (min-width: 351px) and (max-width: 576px) {

    .footer-logo-image {
        width: 30%;
    
    
    }
    

    .footer-links-container {
        width: 90vw;
        font-size: 0.8rem;
    

    }



}

@media screen and (min-width: 577px) and (max-width: 768px) {

    .footer-logo-image {
        width: 30%;
    
    
    }
    

    .footer-links-container {
        width: 70vw;
        font-size: 0.8rem;
    

    }


}


@media screen and (min-width: 769px) and (max-width: 1010px) {

    .footer-logo-image {
        width: 20%;
    
    
    }
    

    .footer-links-container {
        width: 70vw;
        font-size: 1rem;
    

    }

}

@media screen and (min-width: 1011px) and (max-width: 1179px) {


    .footer-links-container {
        width: 50vw;
        font-size: 1rem;
    

    }
}